body {
  background: #004545;
  font-family: monospace, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  padding: 10px;
}

.App {
  text-align: center;
}
.App h1 {
  color: #eeeeee;
  padding: 5px;
  font-size: 1.5rem;
  transition: 0.3s ease-out;
}
h1:hover {
  text-decoration: underline;
  cursor: pointer;
  color: burlywood;
}

::selection {
  background: burlywood;
  color: #333;
}
.container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
  gap: 20px;
  grid-template-rows: minmax(auto-fill, 1fr);
}

.app-wrapper {
  color: #ffffff;
  text-transform: uppercase;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.6);
  padding: 10px;
  line-height: 1.5rem;
  background: #eeeeee;
  color: #939393;
  letter-spacing: 1px;
}

.app-wrapper p {
  color: burlywood;
  font-weight: 900;
  letter-spacing: 2px;
  font-size: 1.1rem;
  border-bottom: 2px solid burlywood;
}

.footer {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: 0;
  left: 0;
  background: #989898;
}
.footer p {
  font-size: 1rem;
  color: #333;
}
